import { createPopper, Instance } from '@popperjs/core';
import moment from "moment";

import clsTimePicker from "./timePicker.classNames";
import elements, { updateI18n, periodGenerator, hourGenerator, minuteGenerator, selectTime, scrollIntoView, setPeriod, setHourId, setMinuteId, getCurrentTime } from "./timePicker.elements";
import * as utils from "./utils";

import { createClickOutsideHandler, createESCHideController } from '@utils';

export declare type IRTimePickerClickEvent = ( _ev: MouseEvent, _value: string ) => void;

export interface IRTimePickerHandlerArgs {
    uuid: string;
    refElement: HTMLElement;
    onChange: IRTimePickerClickEvent;
}

export declare type IRTimePickerHandler = ReturnType<typeof createIRTimePickerHandler>

export function createIRTimePickerHandler( { uuid, refElement, onChange }: IRTimePickerHandlerArgs )
{
    let visible = false;
    let popper: Instance | null = null;

    const handler = {
        setTime( value: string )
        {
            const { hour, minute } = utils.parseTime( value );

            selectTime( hour, minute );
            scrollIntoView( hour, minute );
        },

        show: () =>
        {
            if ( visible ) return;

            popper = createPopper( refElement, elements.timePicker, {
                placement: "bottom-end",
                modifiers: [
                    {
                        name: 'offset',
                        options: {
                            offset: [0, 8],
                        },
                    },
                ],
            } );

            updateI18n();

            visible = true;
            escController.create();
            outsideHandler.create();

            // binding events
            // period
            elements.nowButton.onclick = ( ev ) =>
            {
                const value = moment().format( "HH:mm" );

                handler.setTime( value );
                onChange( ev, value );
            };

            // period events
            for ( const [id, element] of periodGenerator() )
                element.onclick = ( ev ) =>
                {
                    setPeriod( id );
                    onChange( ev, getCurrentTime() );
                };

            // hour events
            for ( const [id, element] of hourGenerator() )
                element.onclick = ( ev ) =>
                {
                    setHourId( id );
                    onChange( ev, getCurrentTime() );
                };

            // minute events
            for ( const [id, element] of minuteGenerator() )
                element.onclick = ( ev ) =>
                {
                    setMinuteId( id );
                    onChange( ev, getCurrentTime() );
                };

            document.body.appendChild( elements.timePicker );
            elements.timePicker.classList.add( clsTimePicker.timePicker['is-visible'] );
            elements.timePicker.style.removeProperty( "display" );
            elements.timePicker.setAttribute( "data-uuid", uuid );

            popper.forceUpdate();
        },

        hide: () =>
        {
            if ( !visible ) return;

            escController.destroy();
            outsideHandler.destroy();
            elements.timePicker.classList.remove( clsTimePicker.timePicker['is-visible'] );

            elements.timePicker.addEventListener( "transitionend", () =>
            {
                visible = false;

                if ( uuid === elements.timePicker.getAttribute( "data-uuid" ) )
                {
                    popper?.destroy();
                    elements.timePicker.style.display = "none";
                }
            }, { once: true } );

        },

        onESC: () => handler.hide(),

        get visible()
        {
            return visible;
        },

    };

    const escController = createESCHideController( handler );
    const outsideHandler = createClickOutsideHandler( {
        eventElements: [elements.timePicker],
        clickOutsideFunc: () => handler.hide(),
    } );

    return handler;
}