
import { IRTreeRenderer, renderDefaultTreeNode } from "./renderer";
import cls from "./tree.classNames";
import { IRTreeNode } from "./treeNode";

import { IRComponent } from '@js-comp/base';
import { appendElement, hasClass } from "@utils";

export interface IRTreeArgs<T> {
    contextElement: HTMLElement;
    data?: T;
    renderer?: IRTreeRenderer<T>;
}

export class IRTree<T = unknown> extends IRComponent
{
    private root: HTMLUListElement;
    private nodes: IRTreeNode<T>[];
    private selectedNode: IRTreeNode<T> | null;
    private renderer: IRTreeRenderer<T>;

    constructor( { contextElement, renderer = renderDefaultTreeNode }: IRTreeArgs<T> )
    {
        super( { contextElement } );
        this.nodes = [];
        this.selectedNode = null;
        this.renderer = renderer;

        contextElement.classList.add( cls.tree.blockElementName );

        this.root = appendElement( contextElement, "ul", cls.treeList.blockElementName );

        this.addCoreElement( this.root );
    }

    get items()
    {
        // 복사본 반환
        return [...this.nodes];
    }

    get selected()
    {
        return this.selectedNode;
    }

    set selected( node: IRTreeNode<T> | null )
    {
        // 똑같은 것을 선택한 경우 pass
        if ( this.selected === node )
            return;

        // 이전 선택된 노드 선택 해제
        this.selectedNode?.release();
        this.selectedNode = node;
        this.onChanged( node );

        if ( node )
        {
            node.select();
            this.onSelectNode( node );
        }

    }

    onDblClick( node: IRTreeNode<T> )
    {
        node.toggle();
    }

    onToggleClick( node: IRTreeNode<T> )
    {
        node.toggle();
    }

    onLabelClick( node: IRTreeNode<T> )
    {
        this.selected = node;
    }

    onSelectNode( _: IRTreeNode<T> )
    {
        // nothing
    }

    onReleaseNode( _: IRTreeNode<T> )
    {
        // nothing
    }

    onRemoveNode( _: IRTreeNode<T> )
    {
        // nothing
    }

    onChanged( _: IRTreeNode<T> | null )
    {
        // nothing
    }

    onExpanding( _: IRTreeNode<T> )
    {
        return true;
    }

    onExpand( _: IRTreeNode<T> )
    {
        // nothing
    }

    onCollapsing( _: IRTreeNode<T> )
    {
        return true;
    }

    onCollapse( _: IRTreeNode<T> )
    {
        // nothing
    }

    onDragStartNode( node: IRTreeNode<T>, ev: DragEvent )
    {
        if ( !ev.dataTransfer ) return;

        ev.dataTransfer.setData( "application/json", JSON.stringify( node.data ) );
        ev.dataTransfer.dropEffect = "copy";
    }

    onDragOverOnNode( _node: IRTreeNode<T>, _ev: DragEvent )
    {
        return false;
    }

    onDropOnNode( _node: IRTreeNode<T>, _ev: DragEvent )
    {
        // nothing
    }

    onRenderNode( _node: IRTreeNode<T>, _element: HTMLElement )
    {
        // nothing
    }

    addChild( parent: IRTreeNode<T> | null, caption: string, data: T )
    {
        const node = new IRTreeNode( {
            parentItem: parent,
            ulContext: parent ? parent.ulTreeList : this.root,
            caption: caption,
            data: data,
            level: parent ? parent.level + 1 : 0,
            renderer: this.renderer,
        } );

        if ( parent )
            parent.addNode( node );

        this.nodes.push( node );

        node.liTreeItem.ondblclick = ( ev ) =>
        {
            this.onDblClick( node );
            ev.stopPropagation();
        };
        node.liTreeItem.onmousedown = ( ev ) =>
        {
            this.onLabelClick( node );
            ev.stopPropagation();
        };
        node.iconExpand.onclick = () => this.onToggleClick( node );
        node.onSelect = () => this.selected = node;
        node.onRelease = ( node ) => this.onReleaseNode( node );
        node.onRemove = () =>
        {
            const id = this.nodes.indexOf( node );

            if ( id !== -1 )
                this.nodes.splice( this.nodes.indexOf( node ), 1 );

            this.onRemoveNode( node );
            // TODO: 현재 선택된 노드 삭제 시 형제 노드 중에 택1, 형제 없을 시, 부모 선택, 부모 없을 시 null 선택
        };
        node.onExpanding = () => this.onExpanding( node );
        node.onExpand = () => this.onExpand( node );
        node.onCollapsing = () => this.onCollapsing( node );
        node.onCollapse = () => this.onCollapse( node );
        node.onDragStart = ( ev ) => this.onDragStartNode( node, ev );
        node.onDrop = ( ev ) => this.onDropOnNode( node, ev );
        node.onDragOver = ( ev ) => this.onDragOverOnNode( node, ev );
        node.onRender = ( element ) => this.onRenderNode( node, element );
        node.render();

        return node;
    }

    selectNode( node: IRTreeNode<T> )
    {
        // 노드가 트리에 포함되어 있어야 선택 가능
        if ( !this.nodes.includes( node ) )
            throw new Error( "Can't select the node because it's not included" );

        this.selected = node;
    }

    clear()
    {
        this.selectedNode = null;
        this.root.innerHTML = "";
        this.nodes.splice( 0, this.nodes.length );
    }

    expandAll()
    {
        this.nodes.forEach( ( node ) => node.expand() );
    }

    collapseAll()
    {
        this.nodes.forEach( ( node ) => node.collapse() );
    }

    expandTo( to: IRTreeNode )
    {
        let node = to.parentItem;

        while ( node )
        {
            node.expand();
            node = node.parentItem;
        }
    }

    getDropNode( ev: DragEvent )
    {
        const label = hasClass( ev.target as HTMLElement, cls.treeLabel.blockElementName, this.contextElement );

        return label !== null ? label.parentElement?.parentElement : null;
    }
}