import _ from 'lodash';
import moment from "moment";

import { createDatePickerHandler, DatePickerHandler } from "./datePicker.handler";

import clsCheckbox from "@components/checkbox/checkbox.classNames";
import clsInput from "@components/input/input.classNames";
import i18n from "@i18n";
import { IRComponent } from '@js-comp/base';

export interface IRDatePickerArgs {
    div: HTMLDivElement;
    wrapper?: Element;
    defaultDate?: Date;
    format?: string;
    checkbox?: boolean;
    disabled?: boolean;
    minDate: Date;
    maxDate: Date;
    onSelect?: ( _date: Date ) => void;
    onChange?: ( _value: string ) => void;
}

export class IRDatePicker extends IRComponent
{
    private selectedDate: Date | null;
    private calendarDate: Date;
    private format: string;

    private div: HTMLDivElement;
    private input: HTMLInputElement;
    private button: HTMLButtonElement;
    private handler: DatePickerHandler;
    private latestValue?: string;
    private _minDate: Date;
    private _maxDate: Date;

    constructor( { div, defaultDate, format = i18n.datePicker.dateFormat, checkbox = false, disabled = false, onSelect, onChange, minDate = new Date( 0 ), maxDate = new Date( "9999-12-31" ) }: IRDatePickerArgs )
    {
        super( { contextElement: div } );

        this.calendarDate = new Date( defaultDate ?? new Date() );
        this.selectedDate = defaultDate ? new Date( defaultDate ) : null;
        this.format = format;
        this.div = div;
        this.input = document.createElement( "input" );
        this.button = document.createElement( "button" );
        this.button.innerHTML = `<i class="ir-icon ir-icon--calendar"></i>`;

        // 기존 내용 제거
        this.div.innerHTML = "";
        this.div.classList.add( clsInput.input.blockElementName );
        this.input.setAttribute( "type", "text" );
        this.input.setAttribute( "placeHolder", this.format );
        this.input.className = clsInput.inputNative.blockElementName;
        this.button.className = clsInput.inputSuffix.blockElementName;

        this._minDate = minDate;
        this._maxDate = maxDate;

        if ( onSelect )
            this.onSelect = onSelect;

        if ( onChange )
            this.onChange = onChange;

        if ( checkbox )
        {
            const divInputPrefix = document.createElement( "div" );
            const checkboxLabel = document.createElement( "label" );
            const inputCheckbox = document.createElement( "input" );

            inputCheckbox.setAttribute( "type", "checkbox" );

            divInputPrefix.classList.add( clsCheckbox.checkbox.blockElementName, clsInput.inputPrefix.blockElementName );
            checkboxLabel.className = clsCheckbox.checkboxWrapper.blockElementName;
            inputCheckbox.className = clsCheckbox.checkboxInput.blockElementName;

            checkboxLabel.appendChild( inputCheckbox );
            divInputPrefix.appendChild( checkboxLabel );
            this.div.appendChild( divInputPrefix );

            inputCheckbox.checked = !disabled;
            inputCheckbox.onchange = () => this.disabled = !inputCheckbox.checked;

            this.addCoreElement( divInputPrefix );
        }

        this.disabled = disabled;
        this.div.appendChild( this.input );
        this.div.appendChild( this.button );
        this.addCoreElement( this.input );
        this.addCoreElement( this.button );

        if ( this.selectedDate )
            this.input.value = moment( this.selectedDate ).format( format );

        this.button.addEventListener( "click", () =>
        {
            if ( this.isVisible === false )
                this.show();
        } );
        this.input.setAttribute( "maxlength", `${format.length}` );
        this.input.addEventListener( "blur", () =>
        {
            const dt = moment( this.input.value, this.format, true );

            this.setDate( dt.isValid() ? dt.toDate() : null );

            if ( this.date )
            {
                if ( this.date < this.minDate || this.date > this.maxDate )
                    this.setDate( null );
            }

        } );

        this.handler = createDatePickerHandler( {
            uuid: this.uuid,
            refElement: this.div,
            minDate: this._minDate,
            maxDate: this._maxDate,
            onClick: ( dt ) =>
            {
                this.setDate( dt );
                this.onSelect( dt );
                this.hide();
            },
        } );
    }

    get textValue()
    {
        return this.input.value;
    }

    get inputCheckbox()
    {
        return this.div.querySelector( ".checkbox__input" ) as HTMLInputElement | null;
    }

    get date()
    {
        return this.selectedDate;
    }

    get isVisible()
    {
        return this.handler.visible;
    }

    get disabled()
    {
        return this.input.hasAttribute( "disabled" );
    }

    get minDate()
    {
        return this._minDate;
    }

    get maxDate()
    {
        return this._maxDate;
    }

    set minDate( value: Date )
    {
        this._minDate = value;
    }

    set maxDate( value: Date )
    {
        this._maxDate = value;
    }

    set disabled( bool: boolean )
    {
        if ( bool )
        {
            this.input.setAttribute( "disabled", "" );
            this.button.setAttribute( "disabled", "" );
        }
        else
        {
            this.input.removeAttribute( "disabled" );
            this.button.removeAttribute( "disabled" );
        }
    }

    setDate( date: Date | null )
    {
        if ( date )
        {
            this.selectedDate = new Date( date );
            this.calendarDate = new Date( date );
            this.input.value = moment( date ).format( this.format );
            this.updateCalendar();
        }
        else
        {
            this.selectedDate = null;
            this.input.value = "";
        }

        if ( this.latestValue !== this.input.value )
            this.onChange( this.input.value );

        this.latestValue = this.input.value;
    }

    show()
    {
        if ( this.isVisible )
            return;

        this.handler.update( this.calendarDate, this.selectedDate );
        this.handler.show();
    }

    hide()
    {
        if ( this.isVisible )
            this.handler.hide();
    }

    /* Object Events */

    onSelect( _date: Date )
    {
        // nothing
    }

    onChange( _value: string )
    {
        // nothing
    }

    private updateCalendar( )
    {
        this.handler.update( this.calendarDate, this.selectedDate );
    }

}