import clsConfirm from "./confirm.classNames";

import clsButton from "@components/button/button.classNames";
import clsDialog from "@components/dialog/dialog.classNames";
import { clsZId } from "@utils";

export const ConfirmButtonTypeList = ["yes", "no", "ok", "cancel", "retry", "ignore", "stop"] as const;

const dialog = document.createElement( "div" );
const dialogWrapper = document.createElement( "div" );
const dialogContent = document.createElement( "div" );
const confirm = document.createElement( "div" );
const confirmContent = document.createElement( "div" );
const confirmIcon = document.createElement( "i" );
const confirmActions = document.createElement( "div" );
const buttonMap: Record<typeof ConfirmButtonTypeList[number], HTMLButtonElement> = {
    cancel: document.createElement( "button" ),
    ignore: document.createElement( "button" ),
    no: document.createElement( "button" ),
    ok: document.createElement( "button" ),
    retry: document.createElement( "button" ),
    stop: document.createElement( "button" ),
    yes: document.createElement( "button" ),
};
const closeIcon = document.createElement( "i" );
const closeButton = document.createElement( "button" );

// class
dialog.classList.add( clsDialog.dialog.blockElementName, clsZId.zIndex.message );
dialogWrapper.className = clsDialog.dialogWrapper.blockElementName;
dialogContent.className = clsDialog.dialogContent.blockElementName;
confirm.className = clsConfirm.confirm.blockElementName;
confirmContent.className = clsConfirm.confirmContent.blockElementName;
confirmActions.className = clsConfirm.confirmActions.blockElementName;

Object.values( buttonMap )
    .forEach( ( e ) => e.classList.add( clsButton.button.blockElementName ) );

buttonMap.yes.classList.add( clsButton.button.primary );
buttonMap.ok.classList.add( clsButton.button.primary );
buttonMap.retry.classList.add( clsButton.button.secondary );
buttonMap.ignore.classList.add( clsButton.button.tertiary );
closeIcon.classList.add( "ir-icon", "ir-icon--close" );
closeButton.className = clsConfirm.confirmButtonClose.blockElementName;

// appending
confirmActions.appendChild( buttonMap.no );
confirmActions.appendChild( buttonMap.stop );
confirmActions.appendChild( buttonMap.ignore );
confirmActions.appendChild( buttonMap.retry );
confirmActions.appendChild( buttonMap.yes );
confirmActions.appendChild( buttonMap.ok );
closeButton.appendChild( closeIcon );
confirm.appendChild( closeButton );
confirm.appendChild( confirmIcon );
confirm.appendChild( confirmContent );
confirm.appendChild( confirmActions );
dialogContent.appendChild( confirm );
dialogWrapper.appendChild( dialogContent );
dialog.appendChild( dialogWrapper );

document.body.append( dialog );

export const elements = {
    dialog,
    confirmIcon,
    confirmContent,
    confirmActions,
    buttonMap,
    closeButton,
};