import { createIRTimePickerHandler, IRTimePickerHandler } from './timePicker.handler';
import * as utils from "./utils";

import clsCheckbox from "@components/checkbox/checkbox.classNames";
import clsInput from "@components/input/input.classNames";
import { IRComponent, IRComponentArgs } from '@js-comp/base';

export interface IRTimePickerArgs extends IRComponentArgs{
    value?: string;
    checkbox?: boolean;
    disabled: boolean;
    onChange?: ( _value: string ) => void;
}

export class IRTimePicker extends IRComponent
{
    private input: HTMLInputElement;
    private button: HTMLButtonElement;
    private timePickerHandler: IRTimePickerHandler;
    private latestValue: string;

    private _value?: string;

    constructor( args: IRTimePickerArgs )
    {
        super( args );

        this.contextElement.className = clsInput.input.blockElementName;

        this.input = document.createElement( "input" );
        this.input.placeholder = "hh:mm";
        this.input.className = clsInput.inputNative.blockElementName;

        this.button = document.createElement( "button" );
        this.button.innerHTML = `<i class="ir-icon ir-icon--watch"></i>`;
        this.button.className = clsInput.inputSuffix.blockElementName;
        this.latestValue = args.value || "";

        this.button.onclick = () => this.toggle();

        this.timePickerHandler = createIRTimePickerHandler( {
            uuid: this.uuid,
            refElement: this.contextElement,
            onChange: ( _, value ) =>
            {
                this.value = value;
                this.onChange( value );
            },
        } );

        if ( args.checkbox )
        {
            const divInputPrefix = document.createElement( "div" );
            const checkboxLabel = document.createElement( "label" );
            const inputCheckbox = document.createElement( "input" );

            inputCheckbox.setAttribute( "type", "checkbox" );

            divInputPrefix.classList.add( clsCheckbox.checkbox.blockElementName, clsInput.inputPrefix.blockElementName );
            checkboxLabel.className = clsCheckbox.checkboxWrapper.blockElementName;
            inputCheckbox.className = clsCheckbox.checkboxInput.blockElementName;

            checkboxLabel.appendChild( inputCheckbox );
            divInputPrefix.appendChild( checkboxLabel );
            this.contextElement.appendChild( divInputPrefix );

            inputCheckbox.checked = !args.disabled;
            inputCheckbox.onchange = () => this.disabled = !inputCheckbox.checked;

            this.addCoreElement( divInputPrefix );
        }

        this.contextElement.appendChild( this.input );
        this.contextElement.appendChild( this.button );

        this.addCoreElement( this.input );
        this.addCoreElement( this.button );

        this.disabled = args.disabled === true;

        if ( args.onChange )
            this.onChange = args.onChange;

        if ( args.value )
            this.value = args.value;

        this.input.onblur = () =>
        {
            if ( !utils.isValidPattern( this.input.value ) )
                this.value = this.latestValue;
            else
                this.value = this.input.value;
        };
    }

    get hour()
    {
        return 0;
    }

    get minute()
    {
        return 0;
    }

    get inputCheckbox()
    {
        return this.contextElement.querySelector( ".checkbox__input" ) as HTMLInputElement | null;
    }

    get visible()
    {
        return this.timePickerHandler.visible;
    }

    get disabled()
    {
        return this.input.disabled;
    }

    get value()
    {
        return this._value;
    }

    set disabled( disabled: boolean )
    {
        this.input.disabled = disabled;
        this.button.disabled = disabled;
    }

    set value( value: string | undefined )
    {
        this._value = value;
        this.input.value = value || "";
        this.latestValue = this.input.value;
    }

    show()
    {
        this.timePickerHandler.show();
        this.timePickerHandler.setTime( this.value || "00:00" );
    }

    hide()
    {
        this.timePickerHandler.hide();
    }

    toggle()
    {
        if ( this.visible )
            this.hide();
        else
            this.show();
    }

    /*
     * Events
     */
    onChange( _value: string )
    {
        // nothing
    }
}