import clsTree from "./tree.classNames";
import type { IRTreeNode } from "./treeNode";

import clsCheckbox from "@components/checkbox/checkbox.classNames";
import { appendElement } from '@utils';

export declare type IRTreeRenderer<T = unknown> = ( node: IRTreeNode<T> ) => HTMLElement;

export function renderDefaultTreeNode<T = unknown>( node: IRTreeNode<T> )
{
    const content = document.createElement( "div" );

    // icon이 있을 때만 추가
    if ( node.iconClass )
    {
        const iconFolder = appendElement( content, "i", clsTree.treeIconFolder.blockElementName, "ir-icon" );

        iconFolder.classList.add( node.iconClass );

        if ( node.iconColor )
            iconFolder.style.setProperty( "--ir-icon-foreground-color", node.iconColor );
    }

    const spanTreeLabel = appendElement( content, "span", clsTree.treeLabelText.blockElementName );

    content.classList.add( clsTree.treeLabel.blockElementName );
    spanTreeLabel.innerText = node.caption;

    return content;
}

interface RenderCheckboxNodeArgs<T> {
    onCheck?: ( node: IRTreeNode<T> ) => void;
}

function createCheckboxElement( checked: boolean )
{
    const label = document.createElement( "label" );
    const checkbox = document.createElement( "input" );

    checkbox.type = "checkbox";
    checkbox.checked = checked;
    label.classList.add( clsCheckbox.checkboxWrapper.blockElementName );
    checkbox.classList.add( clsCheckbox.checkboxInput.blockElementName );

    label.addEventListener( "dblclick", ( ev ) => ev.stopPropagation() );
    label.appendChild( checkbox );

    return { label, checkbox };
}

export function renderCheckboxTreeNode<T = unknown>( { onCheck }: RenderCheckboxNodeArgs<T> )
{

    return ( node: IRTreeNode<T> ) =>
    {
        const content = renderDefaultTreeNode( node );
        const { label, checkbox } = createCheckboxElement( node.checked );

        content.insertAdjacentElement( "afterbegin", label );

        checkbox.addEventListener( "change", () =>
        {
            node.updateCheckedWithPropagation( checkbox.checked );
            node.parentItem?.updateCheckedStatus();
            onCheck && onCheck( node );
        } );

        return content;
    };
}