import clsDialog from "../../components/dialog/dialog.classNames";
import clsConfirm from "./confirm.classNames";
import { elements, ConfirmButtonTypeList } from "./confirm.elements";

import i18n from "@i18n";
import { registerGlobalKeyListener, unregisterGlobalKeyListener, GlobalKeyFunc, createESCHideController, ESCHideController } from "@utils";

export declare type ConfirmType = "info" | "error" | "question" | "warning"

export declare type ConfirmButtonType = typeof ConfirmButtonTypeList[number]

export declare type ConfirmMessageMap = { [_ in ConfirmButtonType]?: string }

export interface IRConfirmArgs {
    iconType?: ConfirmType
    buttonType?: ConfirmButtonType[]
    messageHTML: string
    msgMap?: ConfirmMessageMap
    onClick?: ( _btnType: ConfirmButtonType ) => void
    escButton?: ConfirmButtonType,
    enterButton?: ConfirmButtonType
}

const confirmTypeClass: Record<ConfirmType, string> = {
    error: "ir-icon--cancel-circle-fill ir-icon--danger",
    info: "ir-icon--information-circle-fill ir-icon--info",
    question: "ir-icon--question ir-icon--info",
    warning: "ir-icon--error ir-icon--warning",
};

let lastConfirm: IRConfirm | null = null;

export class IRConfirm
{
    private escController: ESCHideController;

    iconType: ConfirmType;
    buttonType: ConfirmButtonType[];
    messageHTML: string;
    msgMap: ConfirmMessageMap;
    visible: boolean;
    enterButton?: ConfirmButtonType;
    escButton?: ConfirmButtonType;
    enterHandler?: GlobalKeyFunc;
    escHandler?: GlobalKeyFunc;

    constructor( { iconType = "info", buttonType = ["ok"], messageHTML, msgMap = {}, onClick, escButton, enterButton }: IRConfirmArgs )
    {
        this.visible = false;
        this.iconType = iconType;
        this.buttonType = buttonType;
        this.messageHTML = messageHTML;
        this.msgMap = {
            ...msgMap,
        };
        this.enterButton = enterButton;
        this.escButton = escButton;
        this.escController = createESCHideController( this );

        if ( onClick )
            this.onClick = onClick;
    }

    show()
    {
        // 이미  IRConfirm이 열린 상태에선 다른 confirm show 불가
        if ( lastConfirm?.visible )
            return;

        lastConfirm = this;
        this.visible = true;
        elements.confirmContent.innerHTML = this.messageHTML;
        elements.confirmIcon.className = `${clsConfirm.confirmIcon} ir-icon ${confirmTypeClass[this.iconType] || ""}`;
        elements.closeButton.onclick = () => this.hide();

        for ( const key of ConfirmButtonTypeList )
        {
            elements.buttonMap[key].remove();
            elements.buttonMap[key].innerText = this.msgMap[key] ?? i18n.confirm[key];
            elements.buttonMap[key].onclick = () =>
            {
                this.hide();
                this.onClick( key );
            };
        }

        for ( const key of this.buttonType )
            elements.confirmActions.appendChild( elements.buttonMap[key] );

        if ( !elements.dialog.isConnected )
            document.body.appendChild( elements.dialog );
        elements.dialog.classList.add( clsDialog.dialog['is-visible'] );

        elements.dialog.addEventListener( "transitionend", () =>
        {
            this.enterHandler = () =>
            {
                if ( this.enterButton )
                    elements.buttonMap[this.enterButton].click();
                else if ( this.buttonType.length === 1 )
                    elements.buttonMap[this.buttonType[0]].click();
                else
                    this.hide();
            };

            registerGlobalKeyListener( "Enter", this.enterHandler );
        }, { once: true } );

        this.escController.create();

    }

    onESC()
    {
        if ( this.escButton )
            elements.buttonMap[this.escButton].click();
    }

    hide()
    {
        if ( this.visible === false ) return;

        this.escController.destroy();
        lastConfirm = null;
        this.visible = false;
        elements.dialog.classList.remove( clsDialog.dialog['is-visible'] );
        this.enterHandler && unregisterGlobalKeyListener( "Enter", this.enterHandler );
    }

    onClick( _btnType: ConfirmButtonType )
    {
        // nothing
    }

}