import { appendElement } from '../../utils';
import { IRTreeRenderer, renderDefaultTreeNode } from "./renderer";
import cls from "./tree.classNames";

export interface IRTreeNodeArgs<T> {
    parentItem: IRTreeNode<T> | null;
    ulContext: HTMLUListElement;
    caption: string;
    level: number;
    data: T;
    renderer: IRTreeRenderer<T>;
    defaultChecked?: boolean;
    icon?: string;
}

const statusIcon = "ir-icon--triangle-small-right";

export class IRTreeNode<T = unknown>
{
    liTreeItem: HTMLLIElement;
    divTreeItemContent: HTMLDivElement;
    nodeRenderContent: HTMLElement | undefined;
    ulTreeList: HTMLUListElement;
    iconExpand: HTMLElement;
    data: T;
    level: number;

    parentItem: IRTreeNode<T> | null;

    private children: IRTreeNode<T>[];
    private _caption: string;
    private _checked: boolean;
    private _renderer: IRTreeRenderer<T>;
    private _iconClass: string | undefined;
    private _iconColor: string | undefined;

    constructor( { parentItem, ulContext, icon, caption, data, level, defaultChecked = false, renderer = renderDefaultTreeNode }: IRTreeNodeArgs<T> )
    {
        this.children = [];
        this.parentItem = parentItem;
        this.data = data;
        this.level = level;
        this._iconClass = icon;
        this._caption = caption;
        this._renderer = renderer;
        this._checked = defaultChecked;

        this.liTreeItem = appendElement( ulContext, "li", cls.treeItem.blockElementName );
        this.divTreeItemContent = appendElement( this.liTreeItem, "div", cls.treeItemContent.blockElementName );
        this.ulTreeList = appendElement( this.liTreeItem, "ul", cls.treeList.blockElementName );
        this.iconExpand = appendElement( this.divTreeItemContent, "i", cls.treeIconExpand.blockElementName, "ir-icon", statusIcon );

        this.divTreeItemContent.draggable = true;
        this.divTreeItemContent.addEventListener( "dragstart", ( ev ) => this.onDragStart( ev ) );
        this.divTreeItemContent.addEventListener( "dragenter", ( ev ) => this.onDragOver( ev ) && this.divTreeItemContent.classList.add( "is-dragging" ) );
        this.divTreeItemContent.addEventListener( "dragover", ( ev ) => this.onDragOver( ev ) && ev.preventDefault() );
        this.divTreeItemContent.addEventListener( "dragleave", () => this.clearDragging() );
        this.divTreeItemContent.addEventListener( "drop", ( ev ) =>
        {
            try
            {
                this.onDrop( ev );
            }
            finally
            {
                this.clearDragging();
            }
        } );
    }

    get items()
    {
        // 복사본 반환
        return [...this.children];
    }

    get count()
    {
        return this.children.length;
    }

    get caption()
    {
        return this._caption;
    }

    get iconClass()
    {
        return this._iconClass;
    }

    get hasChildren()
    {
        return this.liTreeItem.classList.contains( cls.treeItem.parent );
    }

    get expanded()
    {
        return this.liTreeItem.classList.contains( cls.treeItem['is-expanded'] );
    }

    get selected()
    {
        return this.liTreeItem.classList.contains( cls.treeItem['is-selected'] );
    }

    get iconColor()
    {
        return this._iconColor;
    }

    get checked()
    {
        return this._checked;
    }

    set caption( text: string )
    {
        this._caption = text;
        this.render();
    }

    set checked( checked: boolean )
    {
        this._checked = checked;
        this.render();
    }

    set iconClass( iconClass: string | undefined )
    {
        this._iconClass = iconClass;
        this.render();
    }

    set iconColor( color: string | undefined )
    {
        this._iconColor = color;
        this.render();
    }

    set hasChildren( value: boolean )
    {
        if ( value )
            this.liTreeItem.classList.add( cls.treeItem.parent );
        else
            this.liTreeItem.classList.remove( cls.treeItem.parent );
    }

    removeItems()
    {
        // 올바른 forEach를 위해 얕은 복사 후 delete 수행
        [...this.children].forEach( ( node ) => node.delete() );
    }

    addNode( node: IRTreeNode<T> )
    {
        this.children.push( node );
        // parent 클래스 없는 경우 추가
        if ( !this.hasChildren )
            this.hasChildren = true;
    }

    updateCheckedWithPropagation( checked: boolean )
    {
        this.checked = checked;
        this.children.forEach( ( node ) => node.updateCheckedWithPropagation( checked ) );
    }

    updateCheckedStatus()
    {
        this.checked = this.children.every( ( node ) => node.checked );
    }

    delete()
    {
        // dom 삭제
        this.liTreeItem.remove();
        this.emitDeleteRecursive();

        // 부모 요소 있는 경우, 리스트에서 삭제
        if ( this.parentItem )
            this.parentItem.removeChildNode( this );
    }

    collapse()
    {
        if ( this.expanded )
        {
            if ( this.onCollapsing( this ) )
            {
                this.liTreeItem.classList.remove( cls.treeItem['is-expanded'] );
                this.onCollapse( this );
            }
        }
    }

    scrollIntoView()
    {
        // TODO 스크롤
        this.divTreeItemContent.scrollIntoView();
    }

    expand()
    {
        if ( !this.expanded )
        {
            if ( this.onExpanding( this ) )
            {
                this.liTreeItem.classList.add( cls.treeItem['is-expanded'] );
                this.onExpand( this );
            }
        }
    }

    toggle()
    {
        if ( this.liTreeItem.classList.contains( cls.treeItem['is-expanded'] ) )
            this.collapse();
        else
            this.expand();
    }

    select()
    {
        if ( !this.selected )
        {
            this.liTreeItem.classList.add( cls.treeItem['is-selected'] );
            this.onSelect( this );
        }
    }

    release()
    {
        if ( this.selected )
        {
            this.liTreeItem.classList.remove( cls.treeItem['is-selected'] );
            this.onRelease( this );
        }
    }

    render()
    {
        this.nodeRenderContent?.remove();
        this.nodeRenderContent = this._renderer( this );
        this.divTreeItemContent.appendChild( this.nodeRenderContent );
        this.onRender( this.nodeRenderContent );
    }

    /**
     * Drag Event
     */
    onDragStart( _ev: DragEvent )
    {
        // nothing
    }

    onDrop( _ev: DragEvent )
    {
        // nothing
    }

    onDragOver( _ev: DragEvent )
    {
        return false;
    }

    /**
     * Object Events
     */

    onSelect( _node: IRTreeNode<T> )
    {
        // nothing
    }

    onRelease( _node: IRTreeNode<T> )
    {
        // nothing
    }

    onRemove( _node: IRTreeNode<T> )
    {
        // nothing
    }

    onExpanding( _node: IRTreeNode<T> )
    {
        return true;
    }

    onExpand( _node: IRTreeNode<T> )
    {
        // nothing
    }

    onRender( _element: HTMLElement )
    {
        // nothing
    }

    onCollapsing( _node: IRTreeNode<T> )
    {
        return true;
    }

    onCollapse( _node: IRTreeNode<T> )
    {
        // nothing
    }

    /**
     * 현재 자식 배열에서 해당 node 제거
     */
    private removeChildNode( node: IRTreeNode<T> )
    {
        const id = this.children.indexOf( node );

        if ( id === -1 )
            throw new Error( 'there is not the node' );

        this.children.splice( id, 1 );

        // 자식 아예 없어진 경우 parent 클래스 제거 및 expand 상태 해제
        if ( this.children.length === 0 )
        {
            this.hasChildren = false;
            this.collapse();
        }
    }

    private emitDeleteRecursive()
    {
        // 자식부터 remove 이벤트 발신
        this.children.forEach( ( node ) => node.emitDeleteRecursive() );
        // remove 이벤트 발신
        this.onRemove( this );
    }

    private clearDragging()
    {
        this.divTreeItemContent.classList.remove( "is-dragging" );
    }

}