import { createPopper, Instance } from "@popperjs/core";
import _ from 'lodash';
import moment from "moment";

import cls from "./datePicker.classNames";
import elements from "./datePicker.elements";

import i18n from "@i18n";
import { createClickOutsideHandler, createESCHideController } from "@utils";

export type CalendarClickEvent = ( _dt: Date ) => void;

export type DatePickerHandler = ReturnType<typeof createDatePickerHandler>

export interface DatePickerHandlerArgs {
    uuid: string;
    refElement: HTMLElement;
    onClick: CalendarClickEvent;
    minDate: Date;
    maxDate: Date;
}

const setTimeZero = ( date: Date ) =>
{
    date.setHours( 0 );
    date.setMinutes( 0 );
    date.setSeconds( 0 );
    date.setMilliseconds( 0 );

    return date;
};

export const createDatePickerHandler = ( { uuid, refElement, onClick, minDate, maxDate }: DatePickerHandlerArgs ) =>
{
    let visible = false;
    let popper: Instance | null = null;

    setTimeZero( minDate );
    setTimeZero( maxDate );

    const clickOutsideHandler = createClickOutsideHandler( {
        eventElements: [elements.datePicker],
        clickOutsideFunc: () => handler.hide(),
    } );

    const handler = {
        get visible()
        {
            return visible;
        },
        update( calendarDate: Date, selectedDate: Date | null )
        {
            const tmp = setTimeZero( new Date( calendarDate ) );
            const curMon = tmp.getMonth();

            elements.headerPrev.onclick = () =>
            {
                calendarDate.setDate( 0 );
                handler.update( calendarDate, selectedDate );
            };

            elements.headerNext.onclick = () =>
            {
                calendarDate.setDate( 32 );
                handler.update( calendarDate, selectedDate );
            };

            elements.headerPrevYear.onclick = () =>
            {
                calendarDate.setFullYear( calendarDate.getFullYear() - 1 );
                handler.update( calendarDate, selectedDate );
            };

            elements.headerNextYear.onclick = () =>
            {
                calendarDate.setFullYear( calendarDate.getFullYear() + 1 );
                handler.update( calendarDate, selectedDate );
            };

            elements.todayButton.onclick = () =>
            {
                const today = new Date();

                onClick.call( null, today );
                handler.update( today, today );
            };

            elements.updateI18n();
            elements.headerTitle.innerText = moment( tmp ).format( i18n.datePicker.titleFormat );
            tmp.setDate( 1 );
            tmp.setDate( -( tmp.getDay() - 1 ) );

            elements.contentTableTbody.innerHTML = "";

            _.range( 6 )
                .forEach( () =>
                {
                    const row = document.createElement( "tr" );

                    for ( let i = 0; i < 7; i++ )
                    {
                        const cell = document.createElement( "td" );
                        const tmpDate = new Date( tmp );

                        cell.innerText = `${tmp.getDate()}`;
                        cell.onclick = () => onClick( tmpDate );

                        if ( tmpDate < minDate || tmpDate > maxDate )
                            cell.classList.add( "is-disabled" );
                        else if ( tmp.getMonth() !== curMon )
                            cell.classList.add( "is-diff-month" );
                        else if ( tmp.getTime() === selectedDate?.getTime() )
                            cell.classList.add( "is-selected" );

                        row.appendChild( cell );
                        tmp.setDate( tmp.getDate() + 1 );
                    }

                    elements.contentTableTbody.appendChild( row );
                } );
        },

        show()
        {
            popper = createPopper( refElement, elements.datePicker, {
                placement: "bottom-end",
                modifiers: [
                    {
                        name: 'offset',
                        options: {
                            offset: [0, 8],
                        },
                    },
                ],
            } );

            visible = true;
            document.body.appendChild( elements.datePicker );
            elements.datePicker.style.removeProperty( "display" );
            elements.datePicker.setAttribute( "data-for", uuid );

            clickOutsideHandler.create();
            escController.create();

            requestAnimationFrame( () => elements.datePicker.classList.add( cls.datePicker["is-visible"] ) );
        },
        hide()
        {
            clickOutsideHandler.destroy();
            escController.destroy();

            elements.datePicker.addEventListener( "transitionend", () =>
            {
                visible = false;

                if ( uuid === elements.datePicker.getAttribute( "data-for" ) )
                {
                    popper?.destroy();
                    elements.datePicker.style.display = "none";
                }
            }, { once: true } );

            elements.datePicker.classList.remove( cls.datePicker["is-visible"] );
        },
        onESC: () => handler.hide(),
    };
    const escController = createESCHideController( handler );

    return handler;
};