import _ from "lodash";

import cls from "./datePicker.classNames";

import i18n from "@i18n";
import { clsZId } from "@utils/classNames";

const datePicker = document.createElement( "div" );
const header = document.createElement( "div" );
const headerPrevYear = document.createElement( "button" );
const headerPrev = document.createElement( "button" );
const headerTitle = document.createElement( "strong" );
const headerNext = document.createElement( "button" );
const headerNextYear = document.createElement( "button" );

const prevButtonGroup = document.createElement( "div" );
const nextButtonGroup = document.createElement( "div" );

headerPrevYear.innerHTML = `<i class="ir-icon ir-icon--chevron-double-left"></i>`;
headerPrev.innerHTML = `<i class="ir-icon ir-icon--chevron-left"></i>`;
headerNext.innerHTML = `<i class="ir-icon ir-icon--chevron-right"></i>`;
headerNextYear.innerHTML = `<i class="ir-icon ir-icon--chevron-double-right"></i>`;

const content = document.createElement( "div" );
const contentTable = document.createElement( "table" );
const contentTableThead = document.createElement( "thead" );
const contentTableTbody = document.createElement( "tbody" );

const footer = document.createElement( "div" );

datePicker.classList.add( cls.datePicker.blockElementName, clsZId.zIndex.popover );

/**
 * class init
 */
header.classList.add( cls.datePickerHeader.blockElementName );
headerTitle.classList.add( cls.datePickerHeaderTitle.blockElementName );

headerPrevYear.classList.add( "icon-button", cls.datePickerHeaderButton.blockElementName );
headerPrev.classList.add( "icon-button", cls.datePickerHeaderButton.blockElementName );
headerNext.classList.add( "icon-button", cls.datePickerHeaderButton.blockElementName );
headerNextYear.classList.add( "icon-button", cls.datePickerHeaderButton.blockElementName );

content.classList.add( cls.datePickerContent.blockElementName );
footer.classList.add( cls.datePickerFooter.blockElementName );

/**
 * header init
 */
prevButtonGroup.appendChild( headerPrevYear );
prevButtonGroup.appendChild( headerPrev );
nextButtonGroup.appendChild( headerNext );
nextButtonGroup.appendChild( headerNextYear );

header.appendChild( prevButtonGroup );
header.appendChild( headerTitle );
header.appendChild( nextButtonGroup );

/**
 * content init
 */

const WeekdayList = ["sun", "mon", "tue", "wed", "thu", "fri", "sat"] as const;

const weekdayRow = document.createElement( "tr" );
const weekdayCells: Record<typeof WeekdayList[number], HTMLTableCellElement> = {} as Record<typeof WeekdayList[number], HTMLTableCellElement>;

WeekdayList.forEach( ( w ) =>
{
    const cell = document.createElement( "th" );

    cell.setAttribute( "scope", "col" );

    weekdayCells[w] = cell;
    weekdayRow.appendChild( cell );
} );

contentTableThead.appendChild( weekdayRow );

contentTable.appendChild( contentTableThead );
contentTable.appendChild( contentTableTbody );
content.appendChild( contentTable );

/**
 * footer init
 */
const todayButton = document.createElement( "button" );

todayButton.classList.add( "button" );
todayButton.type = "button";

footer.appendChild( todayButton );

/**
 * appending
 */
datePicker.appendChild( header );
datePicker.appendChild( content );
datePicker.appendChild( footer );

export const updateI18n = () =>
{
    todayButton.innerHTML = ` <i class="button__icon-prefix ir-icon ir-icon--check"></i>${i18n.datePicker.btnToday}`;
    WeekdayList.forEach( ( w ) => weekdayCells[w].innerText = i18n.common[w] );
};

export default {
    datePicker,
    headerTitle,
    contentTableTbody,
    headerPrev,
    headerNext,
    todayButton,
    updateI18n,
    headerNextYear,
    headerPrevYear,
};