import _ from "lodash";

import cls from "./timePicker.classNames";
import { formatTime } from "./utils";

import i18n from "@i18n";
import { clsZId } from "@utils/classNames";

const timePicker = document.createElement( "div" );
const panelWrapper = document.createElement( "div" );
const buttonPanel = document.createElement( "div" );

const panelType = [ "period", "hour", "minute" ] as const;
const panelList = [document.createElement( "div" ), document.createElement( "div" ), document.createElement( "div" )] as const;
const [periodPanel, hourPanel, minutePanel] = panelList;

timePicker.classList.add( cls.timePicker.blockElementName, clsZId.zIndex.popover );

_.range( panelType.length )
    .forEach( ( id ) =>
    {
        panelList[id].setAttribute( "data-type", panelType[id] );
        panelList[id].className = cls.timePickerPanel.blockElementName;
        panelWrapper.appendChild( panelList[id] );
    } );

panelWrapper.className = cls.timePickerPanelWrapper.blockElementName;
buttonPanel.className = cls.timePickerButtonPanel.blockElementName;

const nowButton = document.createElement( "button" );

nowButton.classList.add( "button" );
nowButton.type = "button";

buttonPanel.appendChild( nowButton );

timePicker.appendChild( panelWrapper );
timePicker.appendChild( buttonPanel );

function createPanelItem( text: string )
{
    const panelItem = document.createElement( "div" );

    panelItem.className = cls.timePickerPanelItem.blockElementName;

    if ( text )
        panelItem.innerText = text;

    return panelItem;
}

_.range( 2 )
    .forEach( () => panelList[0].appendChild( createPanelItem( "" ) ) );

_.range( 12 )
    .forEach( ( hour ) => panelList[1].appendChild( createPanelItem( `${hour}` ) ) );

_.range( 60 )
    .forEach( ( minute ) => panelList[2].appendChild( createPanelItem( `${minute}` ) ) );

export function updateI18n()
{
    nowButton.innerHTML = ` <i class="button__icon-prefix ir-icon ir-icon--check"></i>${i18n.timePicker.btnNow}`;
    panelList[0].children[0].innerHTML = i18n.timePicker.am;
    panelList[0].children[1].innerHTML = i18n.timePicker.pm;
}

function removeSelected()
{
    timePicker.querySelectorAll( ".is-selected" )
        .forEach( ( e ) => e.classList.remove( "is-selected" ) );
}

export function getTimeElements( hour: number, minute: number )
{
    const isAM = hour < 12;
    const adjustedHour = isAM ? hour : hour - 12;
    const periodId = isAM ? 0 : 1;

    return {
        periodElement: periodPanel.children[periodId] as HTMLElement,
        hourElement: hourPanel.children[adjustedHour] as HTMLElement,
        minuteElement: minutePanel.children[minute] as HTMLElement,
    };
}

export function scrollIntoView( hour: number, minute: number )
{
    const { hourElement, minuteElement } = getTimeElements( hour, minute );

    hourPanel.scrollTop = hourElement.offsetTop - ( hourPanel.clientHeight / 2 );
    minutePanel.scrollTop = minuteElement.offsetTop - ( minutePanel.clientHeight / 2 );
}

export function selectTime( hour: number, minute: number )
{
    const { periodElement, hourElement, minuteElement } = getTimeElements( hour, minute );

    removeSelected();

    periodElement.classList.add( "is-selected" );
    hourElement.classList.add( "is-selected" );
    minuteElement.classList.add( "is-selected" );
}

export function isAM()
{
    return periodPanel.children[0].classList.contains( "is-selected" );
}

export function setPeriod( periodId: number )
{
    periodPanel.children[( periodId + 1 ) % 2].classList.remove( "is-selected" );
    periodPanel.children[periodId].classList.add( "is-selected" );
}

export function setHourId( hourId: number )
{
    hourPanel.querySelector( ".is-selected" )?.classList.remove( "is-selected" );
    hourPanel.children[hourId].classList.add( "is-selected" );
}

export function setMinuteId( minuteId: number )
{
    minutePanel.querySelector( ".is-selected" )?.classList.remove( "is-selected" );
    minutePanel.children[minuteId].classList.add( "is-selected" );
}

export function *periodGenerator()
{
    yield [0, periodPanel.children[0] as HTMLElement] as const;
    yield [1, periodPanel.children[1] as HTMLElement] as const;
}

export function getCurrentTime()
{
    const hour = parseInt( hourPanel.querySelector( ".is-selected" )?.innerHTML ?? "0" );
    const minute = parseInt( minutePanel.querySelector( ".is-selected" )?.innerHTML ?? "0" );

    return formatTime( hour + ( isAM() ? 0 : 12 ), minute );
}

export function *hourGenerator()
{
    let id = 0;

    for ( const e of hourPanel.children )
        yield [id++, e as HTMLElement] as const;
}

export function *minuteGenerator()
{
    let id = 0;

    for ( const e of minutePanel.children )
        yield [id++, e as HTMLElement] as const;
}

export default {
    timePicker,
    nowButton,
};