import { createPopper, Instance } from "@popperjs/core";
import _ from 'lodash';

import cls from "./select.classNames";

import { IRComponent } from '@js-comp/base';
import { createClickOutsideHandler, createESCHideController, clsZId, ESCHideController, ClickOutsideHandler } from "@utils";

export declare interface IRSelectItem {
    text: string;
    value: string;
    attr: Record<string, string>;
    element: HTMLLIElement;
    option: HTMLOptionElement;
}

export declare interface IRSelectArgs {
    contextElement: HTMLSelectElement;
    onChange?: ( _: HTMLSelectElement ) => void;
    disabled?: boolean;
    defaultValue?: string | null;
}

export function createDropdownDiv()
{
    const divDropdown = document.createElement( "div" );
    const ulItems = document.createElement( "ul" );

    divDropdown.classList.add( cls.selectDropdown.blockElementName, clsZId.zIndex.popover );
    ulItems.className = cls.selectDropdownList.blockElementName;

    divDropdown.appendChild( ulItems );

    return { divDropdown, ulItems };
}

export function createDropdownItem( value: string, text: string )
{
    const item = document.createElement( "li" );
    const button = document.createElement( "button" );
    const icon = document.createElement( "i" );
    const span = document.createElement( "span" );

    span.innerText = text;
    button.type = "button";

    button.className = cls.selectDropdownButton.blockElementName;
    item.className = cls.selectDropdownItem.blockElementName;
    item.setAttribute( "ir-select-value", value );
    icon.classList.add( cls.selectDropdownIcon.blockElementName, "ir-icon", "ir-icon--check" );

    button.appendChild( icon );
    button.append( span );
    item.append( button );

    return { item, button, icon, span };
}

export class IRSelect extends IRComponent
{
    divSelect: HTMLDivElement;
    divInput: HTMLDivElement;
    divDropDown: HTMLDivElement;
    inputValue: HTMLInputElement;
    ulItems: HTMLUListElement;
    select: HTMLSelectElement;

    private items: IRSelectItem[];
    private popper: Instance | null = null;
    private escController: ESCHideController;
    private outsideHandler: ClickOutsideHandler;
    private _value: string;
    private lastClickedOption?: HTMLOptionElement;

    constructor( args: HTMLSelectElement | IRSelectArgs )
    {
        super( {
            contextElement: args instanceof HTMLSelectElement ? args : args.contextElement,
        } );
        this.divSelect = document.createElement( "div" );
        this.divInput = document.createElement( "div" );
        this.inputValue = document.createElement( "input" );

        const dropDownElements = createDropdownDiv();

        this.divDropDown = dropDownElements.divDropdown;
        this.ulItems = dropDownElements.ulItems;
        this.items = [];
        this.select = this.contextElement as HTMLSelectElement;

        const icon = document.createElement( "i" );

        // attribute
        this.inputValue.readOnly = true;
        this.inputValue.type = "text";

        // class
        this.divSelect.classList.add( cls.select.blockElementName );
        this.select.classList.forEach( ( cls ) => this.divSelect.classList.add( cls ) );

        this.divInput.className = cls.selectInput.blockElementName;
        this.inputValue.className = cls.selectInputNative.blockElementName;
        icon.classList.add( cls.selectInputIcon.blockElementName, "ir-icon", "ir-icon--chevron-down" );

        // appending
        this.divInput.appendChild( this.inputValue );
        this.divInput.appendChild( icon );
        this.divDropDown.appendChild( this.ulItems );
        this.divSelect.appendChild( this.divInput );

        const defaultValue = this.select.value;

        this._value = "";

        // create options
        Array.from( this.select.children )
            .map( ( e ) => e as HTMLOptionElement )
            .forEach( ( e ) =>
            {
                const attributes: Record<string, string> = {};

                for ( let i = 0; i < e.attributes.length; ++i )
                {
                    const attr = e.attributes.item( i );

                    if ( !attr ) return;

                    attributes[attr.name] = attr.value;
                }

                this.addItem( e.value, e.text, attributes );
                e.remove();
            } );

        if ( !( args instanceof HTMLSelectElement ) )
        {
            if ( args.onChange )
                this.onChange = args.onChange;

            this.disabled = args.disabled ?? false;

            // 기본 값 있을 시 해당 값으로 초기화
            if ( args.defaultValue !== null )
                this.value = args.defaultValue || defaultValue;
        }
        else
            this.value = defaultValue;

        // select none option 추가
        this.select.insertAdjacentElement( "beforebegin", this.divSelect );
        this.select.style.display = "none";

        this.divInput.onclick = ( ) => this.toggle();
        this.addCoreElement( this.divSelect );

        this.escController = createESCHideController( this );
        this.outsideHandler = createClickOutsideHandler( {
            eventElements: [this.divSelect, this.divDropDown],
            clickOutsideFunc: () => this.hide(),
        } );
    }

    /*
     * Getters
     */

    get listItems()
    {
        return [...this.items];
    }

    get disabled()
    {
        return this.inputValue.hasAttribute( "disabled" );
    }

    get value()
    {
        return this._value;
    }

    get text()
    {
        return this.inputValue.value;
    }

    /*
     * Setters
     */

    set disabled( bool: boolean )
    {
        if ( bool )
            this.inputValue.setAttribute( "disabled", "true" );
        else
            this.inputValue.removeAttribute( "disabled" );
    }

    set value( value: string )
    {
        // 지금 value와 같을 시 pass
        if ( this._value === value ) return;

        // value가 list에 있는지 검사
        if ( this.hasElementByValue( value ) )
            this.selectItem( value );
        else
            throw new Error( `There is no '${value}' value` );
    }

    clearItems()
    {
        this.lastClickedOption = undefined;
        this.items
            .forEach( ( { element, option } ) =>
            {
                element.remove();
                option.remove();
            } );
        this.items = [];
        this.inputValue.value = "";
        this.select.value = "";
        this._value = "";
    }

    removeItem( value: string )
    {
        this.items.filter( ( x ) => x.value === value )
            .forEach( ( { element, option } ) =>
            {
                element.remove();
                option.remove();
            } );

        this.items = this.items.filter( ( x ) => x.value !== value );
    }

    expand()
    {
        if ( this.disabled ) return;

        this.divDropDown.style.width = `${this.divSelect.offsetWidth}px`;

        document.body.appendChild( this.divDropDown );

        this.popper = createPopper( this.divSelect, this.divDropDown, {
            placement: "bottom-end",
            modifiers: [
                {
                    name: 'offset',
                    options: {
                        offset: [0, 8],
                    },
                },
            ],
        } );

        this.divSelect.classList.add( cls.select['is-expanded'] );
        this.divDropDown.classList.add( cls.select['is-expanded'] );

        this.escController.create();
        this.outsideHandler.create();
    }

    hide()
    {
        this.escController.destroy();
        this.outsideHandler.destroy();
        this.divSelect.classList.remove( cls.select['is-expanded'] );
        this.divDropDown.classList.remove( cls.select['is-expanded'] );

        this.divDropDown.addEventListener( "transitionend", () =>
        {
            this.popper?.destroy();
            this.divDropDown.remove();
        }, { once: true } );
    }

    toggle()
    {
        if ( this.divSelect.classList.contains( cls.select['is-expanded'] ) )
            this.hide();
        else
            this.expand();
    }

    addItem( value: string, text: string, attr: Record<string, string> = {} )
    {
        const { item, button } = createDropdownItem( value, text );
        const option = document.createElement( "option" );

        Object.entries( attr )
            .forEach( ( [key, value] ) => option.setAttribute( key, value ) );

        option.value = value;
        option.innerText = text;

        this.select.appendChild( option );
        this.items.push( { text, value, element: item, option, attr } );
        this.ulItems.appendChild( item );

        item.onclick = ( _ev ) =>
        {
            if ( this.lastClickedOption !== option )
            {
                // 값이 다르면 목록 선택
                this.clearSelectedStatus();
                this.changeValue( { text, value, attr, element: item, option } );
            }

            // 목록 닫기
            this.hide();
        };

        return button;
    }

    /*
     * Object Events
     */

    onChange( _select: HTMLSelectElement, _option: HTMLOptionElement )
    {
        // nothing
    }

    setValueWithoutEvent( value: string )
    {
        // 지금 value와 같을 시 pass
        if ( this._value === value ) return;

        // value가 list에 있는지 검사 후 emitEvent false 지정 (이벤트 전파X)
        if ( this.hasElementByValue( value ) )
            this.selectItem( value, false );
        else
            throw new Error( `There is no '${value}' value` );
    }

    /*
     * Private Methods
     */

    private hasElementByValue( value: string )
    {
        return _( this.ulItems.querySelectorAll( `[ir-select-value]` ) )
            .map( ( e ) => e as HTMLElement )
            .some( ( e ) => e.getAttribute( "ir-select-value" ) === value );
    }

    private clearSelectedStatus()
    {
        _( this.items )
            .forEach( ( { element } ) => element.classList.remove( cls.selectDropdownButton['is-selected'] ) );
    }

    private selectItem( value: string, emitEvent = true )
    {
        this.clearSelectedStatus();

        _( this.items )
            .dropWhile( ( item ) => item.value !== value )
            .take( 1 )
            .forEach( ( e ) => this.changeValue( e, emitEvent ) );
    }

    private changeValue( { text, value, option, element }: IRSelectItem, emitEvent = true )
    {
        this.lastClickedOption = option;
        element.classList.add( cls.selectDropdownButton['is-selected'] );
        this.inputValue.value = text;
        this.select.value = value;
        this._value = value;

        // emit일 때만 이벤트 발생
        if ( emitEvent )
            this.onChange( this.select, option );
    }

}