import { BEMClass } from "../../utils";

const block = "datepicker";

export default {
    datePicker: BEMClass( { block, state: ["is-visible"] } ),
    datePickerHeader: BEMClass( { block, element: "header" } ),
    datePickerHeaderTitle: BEMClass( { block, element: "header-title" } ),
    datePickerHeaderButton: BEMClass( { block, element: "header-button", modifier: ["prev", "next"] } ),
    datePickerContent: BEMClass( { block, element: "content" } ),
    datePickerFooter: BEMClass( { block, element: "footer" } ),
    datePickerCell: BEMClass( { block, element: "cell", state: ["is-selected", "is-disabled"] } ),
};